import * as React from "react"
import { useRef, useCallback, useContext } from "react"
import { shouldNodeAnimate, Props, forEachStackChildThatMustAnimate, forEachChildId } from "../utils/shouldNodeAnimate"
import { LayoutIdContext } from "./LayoutIdContext"
import { StackDirection, StackDistribution } from "./Stack/types"
/**
 * @internal
 */
export interface ShouldAnimateContextProps {
    shouldLayoutIdAnimate: (id: string | undefined, layoutId: string, enabled: boolean, props: Props) => boolean
    clearShouldAnimateCache: () => void
    persistShouldAnimateCache: () => void
}

/**
 * @internal
 */
export const ShouldAnimateContext = React.createContext<ShouldAnimateContextProps>({
    shouldLayoutIdAnimate: args => false,
    clearShouldAnimateCache: () => {},
    persistShouldAnimateCache: () => {},
})

/**
 * @internal
 */
export function ShouldAnimateProvider({ children }: { children: React.ReactNode }) {
    const cache = useRef({
        childrenMustAnimate: new Set(),
        lastAnimatedLayoutIds: new Set(),
        currentAnimatedLayoutIds: new Set(),
        currentNotAnimatedLayoutIds: new Set(),
        props: {},
    })

    const { getLayoutId } = useContext(LayoutIdContext)

    const shouldLayoutIdAnimate = useCallback(
        (id: string | undefined, layoutId: string, enabled: boolean, props: Props): boolean => {
            if (!enabled) {
                cache.current.props[layoutId] = props

                return false
            }

            if (cache.current.lastAnimatedLayoutIds.size > 0) {
                cache.current.props[layoutId] = props

                return cache.current.lastAnimatedLayoutIds.has(layoutId)
            }

            const [shouldAnimateSelf, requiresChildrenAnimation, requiresScaleCorrection] = shouldNodeAnimate(
                cache.current.props[layoutId],
                props
            )

            if ((requiresChildrenAnimation || requiresScaleCorrection) && props?.children) {
                forEachChildId(props.children, childId => cache.current.childrenMustAnimate.add(childId))
            } else if (
                cache.current.props[layoutId] &&
                props.children &&
                props.direction &&
                props.distribution &&
                typeof props.direction === "string" &&
                typeof props.distribution === "string"
            ) {
                forEachStackChildThatMustAnimate(
                    props.children,
                    cache.current.props[layoutId].children,
                    props.direction as StackDirection,
                    props.distribution as StackDistribution,
                    cache.current.props,
                    getLayoutId,
                    childId => cache.current.childrenMustAnimate.add(childId)
                )
            }

            cache.current.props[layoutId] = props

            const animates = shouldAnimateSelf || (!!id && cache.current.childrenMustAnimate.has(id))

            if (animates) {
                cache.current.currentAnimatedLayoutIds.add(layoutId)
            } else {
                cache.current.currentNotAnimatedLayoutIds.add(layoutId)
            }

            return animates
        },
        // Never invalidate this callback.
        []
    )

    const clearShouldAnimateCache = useCallback(() => {
        cache.current.childrenMustAnimate.clear()
        cache.current.lastAnimatedLayoutIds.clear()
        cache.current.currentAnimatedLayoutIds.clear()
        // Removing references to props for layoutIds that weren't rendered ensures that if the screen with the deleted props is rendered again,
        // that it has no previous props cached, and so can return early without comparing identical props.
        cache.current.currentNotAnimatedLayoutIds.forEach((layoutId: string) => delete cache.current.props[layoutId])
        cache.current.currentNotAnimatedLayoutIds.clear()
    }, [])

    const persistShouldAnimateCache = useCallback(() => {
        cache.current.lastAnimatedLayoutIds = new Set(cache.current.currentAnimatedLayoutIds)
        cache.current.currentAnimatedLayoutIds.clear()
        cache.current.currentNotAnimatedLayoutIds.clear()
    }, [])

    return (
        <ShouldAnimateContext.Provider
            value={{
                clearShouldAnimateCache,
                persistShouldAnimateCache,
                shouldLayoutIdAnimate,
            }}
        >
            {children}
        </ShouldAnimateContext.Provider>
    )
}
