import { useRef, useContext } from "react"
import { MagicMotionContext } from "../../components/MagicMotionContext"
import { PresenceContext } from "framer-motion"

interface UseMagicMotionProps {
    name?: string
    id?: string
    duplicatedFrom?: string[]
    _canMagicMotion?: boolean
    _magicMotionProps?: MagicMotionProps
    __fromCodeComponentNode?: boolean
    __layoutId?: string
}

interface Cache {
    layoutId: string | null | undefined
    isInCurrentNavigationTarget: boolean
    _shouldAnimate: boolean
}

/**
 * @internal
 */
export function useMagicMotionProps(
    props: UseMagicMotionProps,
    specificLayoutId?: string,
    postfix?: string
): MagicMotionProps | false {
    const {
        name,
        id,
        duplicatedFrom,
        _canMagicMotion = false,
        _magicMotionProps = false,
        __fromCodeComponentNode = false,
        __layoutId,
    } = props
    const {
        enabled,
        getLayoutId,
        layoutOrder,
        isPresent,
        shouldLayoutIdAnimate,
        isInCurrentNavigationTarget,
        isNavigatingWithMagicMotion,
    } = useContext(MagicMotionContext)
    const presence = useContext(PresenceContext)
    const cache = useRef<Cache>({
        layoutId: undefined,
        isInCurrentNavigationTarget: false,
        _shouldAnimate: false,
    })

    if (_magicMotionProps) return false

    const shouldReceiveId = _canMagicMotion || __fromCodeComponentNode
    const layoutId = calculateLayoutId(shouldReceiveId, specificLayoutId || __layoutId, postfix, cache.current, () =>
        getLayoutId({ id, name, duplicatedFrom })
    )

    if (cache.current.layoutId === undefined) cache.current.layoutId = layoutId

    const isAnimatingBackwards = isPresent !== undefined && isPresent === false
    const isAnimatingRemoval = presence !== null && !presence.isPresent

    const _shouldAnimate = calculateShouldAnimate(
        id,
        isInCurrentNavigationTarget,
        isAnimatingBackwards || isAnimatingRemoval,
        cache.current,
        () => shouldLayoutIdAnimate(id, layoutId, !!enabled && !!isNavigatingWithMagicMotion, props)
    )

    if (cache.current._shouldAnimate !== _shouldAnimate) {
        cache.current._shouldAnimate = _shouldAnimate
    }
    if (cache.current.isInCurrentNavigationTarget !== isInCurrentNavigationTarget) {
        cache.current.isInCurrentNavigationTarget = !!isInCurrentNavigationTarget
    }

    if (id && enabled && layoutId) {
        const value: MagicMotionProps = {
            key: id,
            layoutId,
            layoutOrder,
            _shouldAnimate,
        }

        if (isAnimatingBackwards) value.isPresent = false

        return value
    }

    return false
}

/**
 * @internal
 */
export type MagicMotionProps =
    | {
          key: string
          layoutId?: string
          layoutOrder?: number
          isPresent?: false
          _shouldAnimate?: boolean
      }
    | false

function calculateLayoutId(
    _canMagicMotion: boolean,
    existingLayoutId: string | undefined,
    postfix: string | undefined,
    cache: Cache,
    calculate: () => string | null
) {
    if (!_canMagicMotion) return null

    if (cache.layoutId !== undefined) return cache.layoutId

    const result = existingLayoutId || calculate()

    return postfix ? `${result}-${postfix}` : result
}

function calculateShouldAnimate(
    id: string | undefined,
    isInCurrentNavigationTarget: boolean | undefined,
    isRemoving: boolean,
    cache: Cache,
    calculate: () => boolean
) {
    // Rely on new current to determine `shouldAnimate`.
    if (!id || isRemoving || !isInCurrentNavigationTarget) return false

    // Calculate when the screen becomes the "lead".
    if (isInCurrentNavigationTarget !== cache.isInCurrentNavigationTarget) return calculate()

    return cache._shouldAnimate
}

/**
 * @internal
 */
export function useLayoutId(props: UseMagicMotionProps): string | undefined {
    const { name, id, duplicatedFrom } = props
    const { getLayoutId } = useContext(MagicMotionContext)
    const layoutIdRef = useRef<string | undefined>(undefined)
    const newLayoutId = getLayoutId({ id, name, duplicatedFrom })

    if (layoutIdRef.current === undefined && newLayoutId) layoutIdRef.current = newLayoutId

    return layoutIdRef.current
}
