import * as React from "react"
import { isReactElement, isReactChild, assertNever } from "./type-guards"
import type { StackDistribution, StackDirection } from "../components/Stack/types"
import type { BorderProperties } from "../render/style/BorderComponent"
import { isFunction, isNumber, isString, isObject } from "./utils"

type UnknownProps = Record<string, unknown>

interface KnownProps {
    children?: React.ReactNode[]
    style?: React.CSSProperties
    _initialStyle?: React.CSSProperties
    _border?: Partial<BorderProperties>
}

export interface Props extends UnknownProps, KnownProps {}

type AnimatesSelf = boolean
type AnimatesChildren = boolean
type ChildrenRequireScaleCorrection = boolean

export function shouldNodeAnimate(
    previous: Props | undefined,
    current: Props | undefined
): [AnimatesSelf, AnimatesChildren, ChildrenRequireScaleCorrection] {
    if (!previous && !current) return [false, false, false]
    if (!previous) return [false, false, false]

    let shouldAnimateChildren = false
    let shouldAnimateSelf = false

    // If the rect has changed, we can return early knowning that the element needs to animate,
    // and it's children will need to animate to correct scale.
    if (hasRectChanged(previous, current) || hasRectChanged(previous?.style, current?.style)) return [true, false, true]

    // TODO: what's the best way to ensure diff num props implies change?
    // This approach tries to create a unique set of keys without creating new objects that include react children.
    const keys = new Set(Object.keys(previous).concat(Object.keys(current || {})))
    for (const key of keys) {
        const [shouldAnimateForProp, propShouldAnimateChildren] = shouldAnimateForPropKey(key, previous, current || {})
        if (shouldAnimateForProp) shouldAnimateSelf = true
        if (propShouldAnimateChildren) shouldAnimateChildren = true
        if (shouldAnimateForProp && propShouldAnimateChildren) break
    }

    return [shouldAnimateSelf, shouldAnimateChildren, false]
}

const distributionsRequireAllChildrenToAnimate = ["center", "space-around", "space-between", "space-evenly"]

export const forEachStackChildThatMustAnimate = (
    currentChildren: React.ReactNode[],
    previousChildren: React.ReactNode[],
    direction: StackDirection,
    distribution: StackDistribution,
    cache: Record<string, Props>,
    layoutIdForChild: (childProps: any) => string | null,
    callback: (id: string) => void
) => {
    if (!currentChildren || (currentChildren && !currentChildren.length)) return
    // If the number of children have changed, and the stack doesn't have a 'start' or 'end' distribution
    // we will need all children to animate as they will need to move out of the way of the new children.
    if (
        distributionsRequireAllChildrenToAnimate.includes(distribution) &&
        previousChildren.length > 0 &&
        currentChildren.length !== previousChildren.length
    ) {
        forEachChildId(currentChildren, callback)
        return
    }

    const relevantProperty = direction === "horizontal" ? "width" : "height"

    let firstChangedIndex = null

    // Create a lookup table mapping child id's to their props to use in the next step to compare to the current children props.
    const previousChildrenById = {}
    for (let index = 0; index <= previousChildren.length; index++) {
        const child = previousChildren?.[index]
        if (!isReactChild(child) || !isReactElement(child)) continue
        if (child?.props?.id) previousChildrenById[child?.props?.id] = child.props
    }

    // Find the first index of a child who's relevant property has changed between renders.
    for (let index = 0; index < currentChildren.length; index++) {
        const child = currentChildren[index]
        if (!isReactChild(child) || !isReactElement(child)) continue

        // We use the cache to get the last id assigned to the layoutId.
        // We then use that id to lookup that child's props on the last stack's children.
        // This ensure we are comparing like properties. If we instead compared the values saved to the cache
        // by the child component, we would be comparing converted props e.g. "1fr", to unconverted props, e.g. '100%'.
        const layoutId = layoutIdForChild(child?.props)
        if (!layoutId || !cache[layoutId]) continue
        const lastId = cache[layoutId].id
        if (!lastId || !isString(lastId)) continue

        if (previousChildrenById[lastId]?.[relevantProperty] !== child?.props?.[relevantProperty]) {
            firstChangedIndex = index
            break
        }
    }

    if (firstChangedIndex === null) return

    // Depending on the distribution, we can exclude some children from being forced to animate if one of their siblings has changed size.
    const [firstIndex, lastIndex] = firstLastIndexForDistribution(
        distribution,
        firstChangedIndex,
        currentChildren.length
    )

    forEachChildId(currentChildren, callback, firstIndex, lastIndex)
}

export const forEachChildId = (
    children: React.ReactNode | React.ReactNode[],
    callback: (id: string) => void,
    start?: number,
    end?: number
) => {
    const childrenArray = Array.isArray(children) ? children : [children]
    for (let index = start || 0; index <= (end || childrenArray.length); index++) {
        const child = childrenArray?.[index]
        if (!isReactChild(child) || !isReactElement(child)) continue
        if (child?.props?.id) callback(child.props.id)
    }
}

function firstLastIndexForDistribution(
    distribution: StackDistribution,
    firstChangedIndex: number,
    numChildren: number
): [number, number] {
    switch (distribution) {
        case "start":
            return [firstChangedIndex, numChildren]
        case "end":
            return [0, firstChangedIndex]
        case "center":
        case "space-around":
        case "space-between":
        case "space-evenly":
            return [0, numChildren]
        default:
            assertNever(distribution)
    }
}

const propKeysRequireChildAnimations = [
    "gap",
    "distribution",
    "padding",
    "paddingBottom",
    "paddingLeft",
    "paddingTop",
    "paddingRight",
    "alignment",
    "direction",
    "alignItems",
    "flexDirection",
    "justifyContent",
    "padding",
]
function shouldAnimateForPropKey(
    key: string,
    previous: Props | KnownObjects,
    current: Props | KnownObjects
): [boolean, boolean] {
    switch (key) {
        // Do nothing - already handled or expected to change.
        case "children":
        case "id":
        case "duplicatedFrom":
        case "_canMagicMotion":
        case "_magicMotionProps":
        case "name":
            break

        // Known objects
        case "style":
        case "_initialStyle":
        case "_border":
            return shouldAnimateForNestedObject(previous[key], current[key])

        // Object or Value
        case "borderWidth":
            return shouldAnimateForBorderWidth(previous[key], current[key])

        default:
            return shouldAnimateForValues(key, previous[key], current[key])
    }

    return [false, false]
}

function shouldAnimateForBorderWidth(
    previous: BorderProperties["borderWidth"] | undefined,
    current: BorderProperties["borderWidth"] | undefined
): [boolean, boolean] {
    if (typeof previous !== typeof current) return [true, false]

    if (isNumber(previous) && isNumber(current)) return shouldAnimateForValues("borderWidth", previous, current)

    if (isObject(previous) && isObject(current)) return shouldAnimateForNestedObject(previous, current)

    return [false, false]
}

function shouldAnimateForValues(
    key: string,
    previous: Props[keyof Props],
    current: Props[keyof Props]
): [boolean, boolean] {
    // Don't worry about deeply nested props, or actions, currently.
    if (isObject(previous) || isObject(current)) return [false, false]
    if (isFunction(previous) || isFunction(current)) return [false, false]

    if (previous !== current) return propKeysRequireChildAnimations.includes(key) ? [false, true] : [true, false]

    return [false, false]
}

// Ensure borderWidth is an object when handled by shouldAnimateForNestedObject.
type KnownObjects =
    | Required<KnownProps>[keyof Omit<KnownProps, "children" | "borderWidth">]
    | Partial<{
          top: number
          bottom: number
          left: number
          right: number
      }>

function shouldAnimateForNestedObject(
    previous: KnownObjects | undefined,
    current: KnownObjects | undefined
): [boolean, boolean] {
    if (!previous && !current) return [false, false]
    let shouldAnimateChildren = false
    let shouldAnimateSelf = false

    const keys = new Set(Object.keys(previous || {}).concat(Object.keys(current || {})))
    for (const key of keys) {
        const [animates, animatesChildren] = shouldAnimateForPropKey(key, previous || {}, current || {})
        if (animates) shouldAnimateSelf = true
        if (animatesChildren) shouldAnimateChildren = true
        if (shouldAnimateSelf && shouldAnimateChildren) break
    }

    return [shouldAnimateSelf, shouldAnimateChildren]
}

// These helpers get 75% of the way there, but false positive frames that had a width/height, but now use left/right top/bottom.
// It's probably not worth the time to traverse up the tree to find the first parent with width/height,
// and calculate absolute sizes for all ancestors from coordinates to understand if the width change.
function hasRectChanged(
    previous: Props | React.CSSProperties | undefined,
    current: Props | React.CSSProperties | undefined
): boolean {
    if (current === undefined && previous === undefined) return false

    // Any change to width or height means we will likely need to apply scale correction to children.
    if (exists(current?.height) || exists(current?.width) || exists(previous?.height) || exists(previous?.width)) {
        return previous?.height !== current?.height || previous?.width !== current?.width
    }

    // If width and height are undefined, like when a layer is centered absolutely, we compare if those positions changed.
    const hasHorizontalRect = exists(current?.left) && exists(current?.right)
    const hasVerticalRect = exists(current?.top) && exists(current?.bottom)

    // If it doesn't have any positioning, we probably don't need to animate it.
    if (!hasHorizontalRect && !hasVerticalRect) return false

    return (
        (hasHorizontalRect && (previous?.left !== current?.left || previous?.right !== current?.right)) ||
        (hasVerticalRect && (previous?.top !== current?.top || previous?.bottom !== current?.bottom))
    )
}

const exists = (v: any) => v !== undefined
