import * as React from "react"
import { useContext } from "react"
import { Transition } from "framer-motion"
import { GetLayoutId } from "./LayoutIdContext"

/**
 * @internal
 */
export interface MagicMotionContextProps {
    enabled?: boolean
    transition?: Transition
    layoutOrder: number
    isPresent: false | undefined
    isInCurrentNavigationTarget?: boolean
    isNavigatingWithMagicMotion?: boolean
    getLayoutId: (args: GetLayoutId) => string | null
    shouldLayoutIdAnimate: (id: string | undefined, layoutId: string | null, enabled: boolean, props: any) => boolean
}

/**
 * @internal
 */
export const MagicMotionContext = React.createContext<MagicMotionContextProps>({
    enabled: false,
    layoutOrder: 0,
    isPresent: undefined,
    isInCurrentNavigationTarget: true,
    isNavigatingWithMagicMotion: true,
    getLayoutId: args => null,
    shouldLayoutIdAnimate: () => false,
})

/**
 * Used to know if a code component is a child of the current screen.
 * This can be useful for resetting timers or interactions that need to trigger on every navigation.
 * @public
 */
export function useIsInCurrentNavigationTarget() {
    // magicDependency is `true` if the MagicMotionProvider at the root of each screen is the current screen.
    const { isInCurrentNavigationTarget } = useContext(MagicMotionContext)

    return isInCurrentNavigationTarget
}
